body {
  color: #333;
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Open Sans, sans-serif;
}

#navbar {
  background-color: #fff;
  border-bottom: 2px solid #eaeaea;
  justify-content: space-between;
  align-items: center;
  padding: 15px 30px;
  display: flex;
}

.product-grid {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  display: flex;
}

.product-card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #eaeaea;
  flex: 0 calc(33.33% - 20px);
  margin: 10px 0;
  padding: 20px;
  transition: transform .2s ease-in-out;
}

.product-card img {
  width: 100%;
  height: auto;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: none;
  border-radius: 0;
  width: 100%;
  padding: 10px 20px;
  transition: background-color .3s;
}

button:hover {
  background-color: #444;
}

.cart-item-image {
  width: 50px;
  height: auto;
}

#filterdropdown {
  margin: 20px;
}

h3 {
  color: #4caf50;
  text-align: center;
}

input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 200px;
  margin-right: 10px;
  padding: 10px;
}

h2, h3 {
  text-align: center;
  margin: 10px 0;
}
